#slider__clientes {
  padding: $xxxl 100px;

  background: rgb(115, 124, 168);
  background: linear-gradient(
    346deg,
    rgba(115, 124, 168, 1) 0%,
    rgba(31, 41, 99, 1) 32%,
    rgba(3, 4, 36, 1) 100%
  );

  $animationSpeed: 30s;

  // Animation
  @keyframes scroll {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(calc(-250px * 7));
    }
  }
  .slider__clientes-titulo {
    @include titulo();
  }
  .slider {
    padding: $nm;
    background: transparent;
    box-shadow: 0 10px 20px -5px rgba(0, 0, 0, 0.125);
    height: 100px;
    margin: auto;
    overflow: hidden;
    position: relative;
    width: 960px;
    width: inherit;

    &::before,
    &::after {
      content: "";
      height: 100px;
      position: absolute;
      width: 200px;
      z-index: 2;
    }

    &::after {
      right: 0;
      top: 0;
      transform: rotateZ(180deg);
    }

    &::before {
      left: 0;
      top: 0;
    }

    .slide-track {
      animation: scroll $animationSpeed linear infinite;
      display: flex;
      width: calc(250px * 10);
    }

    .slide {
      margin: 0 $sm;
      height: 100px;
      width: 250px;
    }
  }
}
